.block {
  width: 50%;
  position: relative;
  @media screen and (max-width: 700px) {
    width: 100%;
    border: 1px solid green;
    margin: 20px 0; }


  &:nth-child(2n-1) {
    text-align: right;
    left: calc(-25% + 3px);
    //border-right: 3px solid #dbdde5
    margin-top: -50px;
    @media screen and (max-width: 1185px) {
      left: calc(-25% + 2px); }
    @media screen and (max-width: 700px) {
      text-align: center;
      left: 0;
      margin-top: 0;
      border: none; }

    & .blockIcon {
      position: absolute;
      right: -6px; }

    & .blockArrow {
      position: absolute;
      right: 16px; } }

  &:nth-child(2n) {
    left: 25%;
    margin-top: -100px;
    @media screen and (max-width: 700px) {
      text-align: center;
      left: 0;
      margin-top: 0;
      border: none; }

    & .blockIcon {
      position: absolute;
      left: -8px; }

    & .blockArrow {
      position: absolute;
      left: 16px; } }

  &:first-child {
    margin-top: 0;
    padding: 50px 0 0 0;
    @media screen and (max-width: 700px) {
      padding: 0; } }

  .content {
    margin: 20px;
    padding: 10px 20px;
    border-radius: 10px;
    @media screen and (max-width: 700px) {
      margin: 0;
      padding: 0;
      border: 1px solid red; }

    &:hover {
      background-color: white;

      & .blockIcon {
        background-color: #F54F36; }

      & .blockArrow {
        background-color: white; } }

    .blockIcon {
      width: 16px;
      height: 16px;
      border-radius: 15px;
      background-color: #dbdde5;
      z-index: 1;
      @media screen and (max-width: 700px) {
        display: none; } }

    .blockArrow {
      width: 16px;
      height: 16px;
      z-index: 1;
      background-color: #F0F2F7;
      transform: rotate(45deg);
      @media screen and (max-width: 700px) {
        display: none; } }

    .postTitle h4 {
      color: #2c3e50;
      font-size: 22px;
      font-weight: bold;
      padding-bottom: 5px;
      line-height: 1em;
      text-transform: uppercase; }

    .postSubtitle {
      color: #2c3e50;
      font-size: 18px;
      line-height: 28px;
      font-weight: 400;
      padding-bottom: 10px;
      .dot {
        position: relative;
        padding-left: 15px;
        &:before {
          position: absolute;
          content: "";
          display: inline-block;
          width: 5px;
          height: 5px;
          line-height: 1;
          background-color: #F54F36;
          left: 5px;
          margin-top: 12px;
          border-radius: 50%; } } }


    .postContent {
      color: #7d878f;
      font-size: 14px;
      line-height: 20px; } } }
